import React from 'react';
import PropTypes from 'prop-types'
import clockIcon from "../clock-icon.png"
import "./TimeTravelButton.css"

function TimeTravelButton({ onClick, isEnabled }) {
	return (
		<div class={isEnabled?"time-travel-button":"time-travel-button disabled"} onClick={onClick}>
			<img alt="Voltar à ultima decisão" src={clockIcon}/>
		</div>
	)
}

TimeTravelButton.propTypes = {
	onClick: PropTypes.func.isRequired,
	isEnabled: PropTypes.bool
}

export default TimeTravelButton