function getElementHeight(element) {
	if (!(element instanceof HTMLElement)) {
		throw new Error("Element is not an valid DOM Element");
	}
	if (!element.clientHeight || element.clientHeight <= 10) {
		return element.getBoundingClientRect().height;
	}
	return element.clientHeight;
}

async function wait(ms = 0) {
	if (ms === 0) {
		return new Promise(resolve => window.requestAnimationFrame(window.requestAnimationFrame.bind(window, resolve)));
	}
	return new Promise(resolve => window.setTimeout(resolve, ms));
}

export default { getElementHeight, wait };