import StoryData from "./StoryData.json"

export default class StoryModules {
	static load(nameId) {
		if (!StoryData[nameId]) {
			throw new Error("Name id \""+nameId+"\" not found in story data");
		}
		if (!StoryData[nameId].id) {
			StoryData[nameId].id = nameId
		}
		return StoryData[nameId];
	}
}
