import React, {Component} from 'react'
import StoryModule from '../modules/StoryModule.js'
import StoryLine from './StoryLine.js'
import './StoryView.css'

class StoryView extends Component {
	constructor(props) {
		super(props);
		this.state = {
			storyLines: [StoryModule.load("root")],
			takenOptions: []
		};
		this.addLine = this.addLine.bind(this);
	}
	render() {
		return (
			<div className='story-view'>
				{
					this.state.storyLines.map((lineData,i) =>
						<StoryLine
							html={lineData.html}
							actions={i === this.state.storyLines.length-1 ? (lineData.actions || []) : []}
							nameId={lineData.id}
							onNavigateStory={this.addLine}
							key={i}
						/>
					)
				}
			</div>
		);
	}
	addLine(newNameId) {
		const newStoryLines = [...this.state.storyLines];
		try {
			newStoryLines.push(StoryModule.load(newNameId));
		} catch (err) {
			newStoryLines.push({
				"key": "not-found",
				"type": "markdown",
				"html": "<div class='story-not-finished'>- ATENÇÃO LEITOR -\n\nInfelizmente este caminho não foi finalizado ou escrito, ou seja, ele foi planejado mas o autor não pôde terminar ele a tempo de você acessa-lo.\n\nDesculpe!</div>",
				"actions": [
					{
						"text": "Voltar para a opção anterior",
						"link": this.state.takenOptions[this.state.takenOptions.length-1]
					}
				]
			})
		}

		const newTakenOptionsList = [...this.state.takenOptions];
		newTakenOptionsList.push(newNameId);

		this.setState({
			currentIndex: newNameId,
			takenOptions: newTakenOptionsList,
			storyLines: newStoryLines
		});
	}
}

export default StoryView;