import React, { Component } from 'react';
// import logo from './logo.svg';
import './App.css';
import StoryView from "./StoryView.js"
import FixedScreenElement from "./FixedScreenElement.js"
import TimeTravelButton from "./TimeTravelButton.js"
import frontImage from "../front-image.jpg"

class App extends Component {
	constructor(props) {
		super(props);
		this.state = {canTimeTravel: true};
		this.onTimeTravelClick = this.onTimeTravelClick.bind(this);
	}
	render() {
		//const fixedScreenPositionUnit = (window.innerWidth > window.innerHeight) ? "vh" : "vh";
		//const fixedScreenPositionPercent = (window.innerWidth < 600) ? "1" : "10";
		return (
			<div className='app'>
				<div className="front-image-wrapper">
					<div class="front-image" />
				</div>
				<h1 className="title">Interactive Text Adventure</h1>
				<StoryView />
			</div>
		);
	}
	onTimeTravelClick() {
		this.setState({
			canTimeTravel: !this.state.canTimeTravel
		});
	}
}
export default App;
