import React, { Component, Fragment } from 'react'
import PropTypes from 'prop-types'
import './StoryLine.css'
import ButtonLink from './ButtonLink.js'
import DomManipulation from '../modules/DomManipulation.js'

class StoryLine extends Component {
	constructor(props) {
		super(props);
		this.state = {
			stateName: "starting",
			htmlSize: null,
			htmlActionSize: null,
			timerId: null
		}
		this.fixedSizeRef = React.createRef();
		this.freeSizeRef = React.createRef();
	}
	shouldComponentUpdate(props, nextState) {
		return true;
	}
	async componentDidMount() {
		const freeSizeRef = this.freeSizeRef.current;
		if (this.state.stateName === "starting") {
			await DomManipulation.wait(0);
			const elementSize = DomManipulation.getElementHeight(freeSizeRef);
			this.setState({
				stateName: "actionize",
				htmlSize: elementSize,
				htmlActionSize: null,
				timerId: window.setInterval(this.checkForSizeChange.bind(this), 20)
			});
		}
	}
	checkForSizeChange() {
		const freeSizeRef = this.freeSizeRef.current;
		const elementSize = DomManipulation.getElementHeight(freeSizeRef);
		window.clearInterval(this.state.timerId);
		if (this.state.stateName === "actionize") {
			if (this.state.htmlSize !== elementSize) {
				this.setState({
					stateName: "expanding",
					htmlSize: this.state.htmlSize,
					htmlActionSize: elementSize,
					timerId: null
				});
				window.setTimeout(this.postExpansion.bind(this), 100);
			}
		} else {
			console.warn(`Got unexpected state: ${this.state.stateName}`);
		}
	}
	postExpansion() {
		this.setState({
			stateName: "finishing"
		})
	}

	componentWillUnmount() {
		if (this.state.timerId) {
			window.clearInterval(this.state.timerId);
		}
	}

	render() {
		let { html, actions, nameId, onNavigateStory } = this.props;
		if (typeof(html) === "string" && html.includes && html.includes("\n\n")) {
			html = "<p>"+html.split("\n\n").join("</p><p>")+"</p>";
		}
		const styleObj = {
			height: "auto"
		}
		let gradientHiderHeight = 0;
		if (this.state.stateName === "starting") {
			styleObj.height = 0;
			gradientHiderHeight = 100;
			return (
				<Fragment>
				<div ref={this.fixedSizeRef} style={styleObj} className="story-line fixed-size state-starting">
					<div ref={this.freeSizeRef} className="free-size story-wrapper">
						<p class="story-text" dangerouslySetInnerHTML={{__html: html}} />
					</div>
				</div>
					<div className="gradient-hider" style={{height: "100px"}}></div>
				</Fragment>
			);
		}
		if (this.state.stateName === "actionize") {
			styleObj.height = (this.state.htmlSize?this.state.htmlSize+"px":"auto");
			gradientHiderHeight = this.state.htmlSize ? Math.max(this.state.htmlSize, 100) : 100;
			return (
				<Fragment>
					<div ref={this.fixedSizeRef} style={styleObj} className="story-line fixed-size state-actionize">
						<div ref={this.freeSizeRef} className="free-size story-wrapper">
							<p class="story-text" dangerouslySetInnerHTML={{__html: html}} />
							<p class="story-action" style={{opacity: "0.1"}}>{actions.map((action, i) => {
								const callback = e => onNavigateStory(typeof(action) == "string" ? action : action.link);
								const text = typeof(action) == "string" ? action : action.title || action.text;
								return <ButtonLink onClick={callback} key={i}>{text}</ButtonLink>
							})}</p>
						</div>
					</div>
					<div className="gradient-hider" style={{display: (actions.length ? "block" : "none"), height: gradientHiderHeight + "px"}}></div>
				</Fragment>
			);
		}
		if (actions.length !== 0) {
			styleObj.height = (this.state.htmlActionSize?this.state.htmlActionSize+"px":"auto");
		}
		gradientHiderHeight = this.state.stateName === "finishing" ? 0 : this.state.htmlActionSize;
		return (
			<Fragment>
				<div ref={this.fixedSizeRef} style={styleObj} className={`story-line fixed-size id-${nameId} state-${this.state.stateName}`}>
					<div ref={this.freeSizeRef} className="free-size story-wrapper">
						<p class="story-text" dangerouslySetInnerHTML={{__html: html}} />
						<p class="story-action">{actions.map((action, i) => {
							const callback = e => onNavigateStory(typeof(action) == "string" ? action : action.link);
							const text = typeof(action) == "string" ? action : action.title || action.text;
							return <ButtonLink onClick={callback} key={i}>{text}</ButtonLink>
						})}</p>
					</div>
				</div>
				<div className={this.state.stateName === "finishing" ? "gradient-hider with-transition":"gradient-hider"} style={{display: (actions.length ? "block" : "none"), height: gradientHiderHeight + "px"}}></div>
			</Fragment>
		);
	}
}

StoryLine.propTypes = {
	html: PropTypes.string.isRequired,
	actions: PropTypes.array.isRequired,
	onNavigateStory: PropTypes.func.isRequired,
}

export default StoryLine